<template>
    <view v-if="this.bodyData.length > 0">
        <view class="box-in">
            <view class="box-in-in-3">
                <view :style="{
                    'opacity': this.requestParam.emotionalOrientationCodeList.length > 0 ? '1' : '.5',
                }" class="box-in-in-3-title">
                    情感取向-多选 <text style="float: right;font-size: 24rpx;">非必填</text>
                </view>
                <view class="box-in-in-3-list">
                    <view class="box-in-in-3-list-item" :style="{
                        'border': isHas(item.code) ? '5rpx solid #333' : '',
                    }" v-for="(item, index) in bodyData" :key="index" @click="check(item.code)">
                        <view :style="{
                            'opacity': isHas(item.code) ? '1' : '.5',
                        }" class="box-in-in-3-list-item-text">{{ item.name }}</view>
                    </view>
                </view>
            </view>
            <view v-if="loading" style="
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    top: 0;
                    left: 0;
                    background-color: rgba(255,255,255,.4);
                    "></view>
        </view>
    </view>
</template>

<script>
export default {
    data() {
        return {
            loading: false,
            requestParam: {
                emotionalOrientationCodeList: []
            },
            emotionalOrientationCodeListSelect: [],
            /**
             * 情感取向列表
             */
            bodyData: [

            ]
        }
    },

    mounted() {
    },

    methods: {
        open(gender, sexualOrientationCode) {
            this.getBodyData(gender, sexualOrientationCode);
        },
        check(code) {
            if (this.emotionalOrientationCodeListSelect.indexOf(code) > -1) {
                this.emotionalOrientationCodeListSelect.splice(this.emotionalOrientationCodeListSelect.indexOf(code), 1);
            } else {
                this.emotionalOrientationCodeListSelect.push(code);
            }
            this.requestParam.emotionalOrientationCodeList = [];
            // 如果bodyData中存在emotionalOrientationCodeListSelect的code就添加进this.requestParam.emotionalOrientationCodeList
            this.bodyData.forEach((item) => {
                if (this.emotionalOrientationCodeListSelect.indexOf(item.code) > -1) {
                    this.requestParam.emotionalOrientationCodeList.push(item.code);
                }
            });

            this.$emit('checkEmotionalOrientationList', this.requestParam.emotionalOrientationCodeList);
        },
        isHas(code) {
            return this.emotionalOrientationCodeListSelect.indexOf(code) > -1;
        },
        getBodyData(gender, sexualOrientationCode) {
            this.loading = true;
            this.$kt.request.request({
                api: this.$api.business.ovo.emotionalOrientation.getByGenderAndSexualOrientationCode,
                data: {
                    gender,
                    sexualOrientationCode
                },
                stateSuccess: (res) => {
                    this.bodyData = res.data;
                    setTimeout(() => {
                        this.requestParam.emotionalOrientationCodeList = [];
                        // 如果bodyData中存在emotionalOrientationCodeListSelect的code就添加进this.requestParam.emotionalOrientationCodeList
                        this.bodyData.forEach((item) => {
                            if (this.emotionalOrientationCodeListSelect.indexOf(item.code) > -1) {
                                this.requestParam.emotionalOrientationCodeList.push(item.code);
                            }
                        });
                        
                        this.loading = false;
                        this.$emit('checkEmotionalOrientationList', this.requestParam.emotionalOrientationCodeList);
                        this.$emit('getByGenderAndSexualOrientationCodeFinally');
                    }, 300);
                }
            });
        },

    }
}
</script>

<style lang="scss" scoped>
.box-in {
    position: relative;
    text-align: center;
    border-radius: 20rpx;

    .box-in-in-3 {
        border-radius: 10rpx;
        padding: 10rpx;

        .box-in-in-3-title {
            font-size: 30rpx;
            color: #666;
            text-align: left;
            margin-bottom: 20rpx;
            // margin-left: 10rpx;
        }

        .box-in-in-3-list {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            align-items: center;
        }

        .box-in-in-3-list-item {
            width: calc(33.33% - 20rpx);
            margin-bottom: 20rpx;
            border: 5rpx solid #f0f0f0;
            padding: 30rpx 10rpx 30rpx 10rpx;
            border-radius: 20rpx;
            text-align: center;
            box-sizing: border-box;
        }

        .box-in-in-3-list-item-text {
            font-size: 30rpx;
            color: #333;
            opacity: .5;
        }
    }

}
</style>